.header-div {
    width: 100%;
    height: 60px;
    padding-top: 0.5rem;
    font-family: 'Open Sans';
}

.header-div-container-mantine {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.header-div-container {
    padding: 0 3rem;
    margin-top: 1rem;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    font-family: 'Open Sans';
}

.header-div-container-logo {
    height: 100%;
    width: 160px;
}

.header-div-container-logo img {
    width: 100%;
    height: 100%;
}

.header-div-container-search-paper {
    background-color: #F6F6F6;
    display: flex;
    align-items: center;
    border-radius: 3px;
    outline: none;
    margin-left: 3.3rem;
    border: 0.4px solid var(--Extra-Light-Gray);
}

.header-div-container-search-category-select .mantine-1cn2mlo {
    border-left: 0;
    border-top: 0;
    border-bottom: 0;
    border-right: 1px solid var(--Extra-Light-Gray);
    border-radius: 0;
    width: 175px;
    height: 40px;
    color: var(--Para-Color);
    background-color: transparent;
    font-family: 'Open Sans';
    font-size: 13px;
}

.header-div-container-search-input .mantine-gszoqu {
    width: 400px;
    border: 0;
    color: var(--Para-Color);
    font-family: 'Open Sans';
    background-color: transparent;
}

.header-div-container-search-button {
    background: transparent;
    margin: 0;
    padding: 0px 10px;
    cursor: pointer;
    border-radius: 0px;
}

.header-div-container-search-button:hover {
    background: transparent;
}

.header-div-container-cart-wishlist-sell-now {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 14px;
}

.header-div-container-cart-wishlist-sell-now-cart {
    display: flex;
    align-items: center;
    cursor: pointer;
    gap: 0.3rem;
}

.header-div-container-cart-wishlist-sell-now-cart-image {
    width: 25px;
}

.header-div-container-cart-wishlist-sell-now-cart-image img {
    width: 100%;
    height: 100%;
}

.header-div-container-cart-wishlist-sell-now-cart-content {
    color: var(--Para-Color);
}

.header-div-container-cart-wishlist-sell-now-wishlist {
    display: flex;
    align-items: center;
    cursor: pointer;
    gap: 0.3rem;
}

.header-div-container-cart-wishlist-sell-now-wishlist-image {
    width: 25px;
}

.header-div-container-cart-wishlist-sell-now-wishlist-image img {
    width: 100%;
    height: 100%;
}

.header-div-container-cart-wishlist-sell-now-wishlist-content {
    color: var(--Para-Color);
}

.header-div-container-cart-wishlist-sell-now-sellnow {
    width: 75px;
    height: 36px;
    display: flex;
    align-items: center;
}

.header-div-container-cart-wishlist-sell-now-sellnow-button {
    border: 0;
    color: white;
    background-color: var(--Logo-Color6);
    cursor: pointer;
    font-size: 14px;
    /* font-family: 'Open Sans'; */
}

.header-div-container-cart-wishlist-sell-now-sellnow-button:hover {
    background-color: var(--Logo-Color1);
}


/* 

 */

.search-engine-right p {
    font-size: 17px;
    color: #666 !important;
}

