    .header-div-container-logo {
        width: 100% !important;
    }

    .header-div-container-search-category-select .mantine-1cn2mlo {
        width: 100% !important;
    }

    .header-div-container-search-input .mantine-gszoqu {
        width: 100% !important;
    }

    .header-div-container-cart-wishlist-sell-now-cart-image {
        width: 100% !important;
    }

    .header-div-container-cart-wishlist-sell-now-wishlist-image {
